import React from 'react'
import css from './css.css'
import score from './icon@2x.png'
import DocumnetTitle from 'react-document-title';
import {AjaxSend,formats,to_fixed4} from '../../../util/util';


class Exchange extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      type:0,
      data : []
    };
  }

  componentDidMount() {
	const data = {
  		_cmd: 'oec_quot_getMatchDetails',
  		orderId : this.props.location.state.orderId
    };
    
	AjaxSend(data, (da) => {
		let data = da.data;
		let list = [];
		for (let k in data) {
			let a = [];
	      	a = data[k];
	      	a.time = formats(data[k].matchTime);
	      	list[k] = a;
	    }
		console.log(list)
		this.setState({ 
			data: list
		});
    }, (e) => {
      alert(e);
    })
  }


  render() {
  	const { data } = this.state;
  	const type = {
  		1 : "兑换总值",
  		2 : "转让个数"
  	}
  	const type1 = {
  		1 : "兑换值",
  		2 : "转让值"

  	}
  	console.log(this.state.data)
  	let List = this.state.data.map(function (item,index) {
			return (
				<div key={index} className="detail_list flex-column">
	            <div className="exchange_value flex-row-between">
	              <div className="exchange_value1">{type1[item.bsFlag]} <span className="num_color">{(item.orderPrice/10000).toFixed(4)}</span> </div>
								<div className="exchange_time">{type[item.bsFlag]} {item.bsFlag==1?<span className="num_color">{(item.totalAmount+item.fee).toFixed(4)}</span>:<span className="num_color">{(item.totalMatchQty).toFixed(4)}</span>}</div>
	            </div>
	            <div className="exchange_value2 flex-row-between">
								<div className="exchange_time">{item.time}</div>
								<div className="exchange_value_item flex-row-between">
									<img src={score} alt=""/>
									<span>获得{item.bsFlag==1?'OEC':'汇积分'} {item.bsFlag==1?to_fixed4(item.totalMatchQty):to_fixed4((item.totalAmount*10000-item.fee*10000)/10000)}</span>
								</div>
	            </div>
	            <div className="exchange_value3 flex-row-between">
	              <div className="exchange_fee">手续费1% ({(item.fee).toFixed(4)})</div>
	            </div>
	          </div>
			)
		})
		var a = this.state.data
    return (
      <div className="deal_exchange_detail">
        {a.length>0&&a[0].bsFlag==1?
          <DocumnetTitle title="兑换详情"/>:
          <DocumnetTitle title="转让详情"/>}
          {List}
      </div>
    )
  }
}

export default Exchange



