import React from 'react';
import DocumentTitle from "react-document-title";
import cardImg from './img/card@2x.png'
import arrowImg from './img/right@2x.png'
import './css.css'
import PriceForm from './subpage/CashForm'
import { Toast} from 'antd-mobile'
import {AjaxSend,to_fixed2} from "../../util/util"



class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      bank_info:{},
      balance:'',
      price:'',
    }
  }
  componentDidMount() {
    let _this = this
    const data = {
      _cmd:'app_withdraw',
    }
    AjaxSend(data, (da) => {
      _this.setState({
        bank_info : da.data.info,
        balance : to_fixed2(da.data.balance),
      });
    }, (e) => {
      Toast.fail(e);
    })
  }
  set_price(e){
    this.setState({
      price:e
    })
  }
  commit(){
    console.log(this.state.price)
    if(this.state.price>this.state.balance){
      Toast.info('余额不足')
      return
    }
    if(this.state.price<5){
      Toast.info('提现金额最少5元')
      return
    }
    if(this.state.price>50000){
      Toast.info('提现金额最多50000元')
      return
    }

    const data = {
      _cmd:'app_withdraw_apply',
      amount:this.state.price
    }
    AjaxSend(data, (da) => {
        Toast.success('申请成功')
    }, (e) => {
      Toast.fail(e);
    })
  }
  render() {
    let info = this.state.bank_info;

    return (
      <div className="cash_withdrawals">
        <DocumentTitle title="现金交易提现"/>
        <div className="cash_bank flex-row-between">
          <div className="cash_bank_left flex-row-between">
            <img src={cardImg} alt="" className="card_img"/>
            <div className="cash_bank_info">
              <div className="bank_user_name">{info&&info.name?info.name:''}</div>
              <div className="bank_name">{info&&info.bankName?info.bankName:''}({info&&info.bankaccountNo?info.bankaccountNo:''})</div>
            </div>
          </div>
          <img src={arrowImg} alt="" className="arrow_img"/>
        </div>
        <PriceForm balance={this.state.balance} price={this.state.price} set_price={this.set_price.bind(this)}/>
        <div className="tip">* 每笔提现手续费用为3元(无金额限制)，在提现金额中扣除</div>
        <div className="btn" onClick={this.commit.bind(this)}>即刻到账，确认提现</div>
      </div>
    );
  }
}

export default HomeIndex




