import React from 'react';
import {withRouter} from 'react-router-dom'
import DocumentTitle from "react-document-title";
import { List,Toast } from 'antd-mobile';
import {AjaxSend,to_fixed2} from "../../util/util"
import billImg from './img/bill@2x.png'
import subsidiesImg from './img/subsidies@2x.png'
import OECImg from './img/OEC1@2x.png'
import integralImg from './img/integral@2x.png'
import './css.css'

const Item = List.Item;


class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      info:{},
      wallet:{},
    }
  }
  componentDidMount() {
    let _this = this;
    const data = {
      _cmd: 'app_index',
    };
    AjaxSend(data, (da) => {
      _this.setState({
        info : da.data.info,
        wallet : da.data.wallet,
      });
    }, (e) => {
      Toast.fail(e);
    })
  }
  toReocrd1(){
    var data = {type:11};
    var path = {
      pathname:'/score_detail',
      state:data,
    }
    this.props.history.push(path);
  }

  toReocrd2(){
    var data = {type:10};
    var path = {
      pathname:'/score_detail',
      state:data,
    }
    this.props.history.push(path);
  }
  render() {
    let info = this.state.info
    let wallet = this.state.wallet
    return (
      <div className="financial_management">
        <DocumentTitle title="财务管理"/>
        <div className="header flex-column">
          <img src={info&&info.logo?info.logo:''} alt="" className="userPhoto"/>
          <div className="money_text">{info&&info.corpman_name?info.corpman_name:''}</div>
        </div>
        <List>
          <Item
            thumb={billImg}
            arrow="horizontal"
            onClick={() => {this.props.history.push('/user/bill')}}
          >补贴对账单</Item>
          <Item
            thumb={subsidiesImg}
            onClick={() => {this.props.history.push('/user/transfer_money')}}
            arrow="horizontal"
          >
            每日补贴列表
          </Item>
          <Item
            thumb={integralImg}
            onClick={this.toReocrd1.bind(this)}
            arrow="horizontal"
            extra={wallet&&wallet.hjf>=0?wallet.hjf:''}
          >
            汇积分余额
          </Item>
          <Item
            thumb={OECImg}
            onClick={this.toReocrd2.bind(this)}
            arrow="horizontal"
            extra={wallet&&wallet.oec>=0?wallet.oec:''}
          >
            OEC余额
          </Item>
        </List>
        <div className="to_tip flex-row-between" onClick={() => {this.props.history.push('/problem')}}>
          <div className="pro_text">小提示：汇积分的余额可以抵扣很多物美价廉的商品哦！</div>
        </div>
      </div>
    );
  }
}

export default withRouter(HomeIndex)




