import React from 'react';
import DocumentTitle from "react-document-title";
import './css.css'
import { List } from 'antd-mobile';
import headerImg from './img/xybg@2x.png'
import xinyongImg from './img/xinyong.png'
import {AjaxSend} from "../../util/util"

const Item = List.Item;

class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={

    }
  }
  componentDidMount() {
    let data = {
      _cmd:'app_messages'
    }
    AjaxSend(data,(da)=>{
        console.log(da)
    },(err)=>{

    })
  }
  render() {
    return (
      <div className="credit_cont">
        <DocumentTitle title="信用评级"/>
        <div className="header flex-column">
          <div className="credit_num">100</div>
          <div className="credit_title">信用良好</div>
        </div>
        <div className="list_title_cont flex-row-between">
          <img src={xinyongImg} alt=""/>
          <div className="list_title">信用记录</div>
        </div>
        <List className="my-list">
          <Item extra={ <div className="text_color1">-10</div> }>现金延时交付</Item>
          <Item extra={ <div className="text_color2">+10</div> }>都爱泡泡烤鱼现金交易</Item>
        </List>
      </div>
    );
  }
}

export default HomeIndex




