import React from 'react'
import {withRouter} from 'react-router-dom'
import css from './css.css'


class Exchange extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    };
  }

  componentDidMount() {

  }

  to_record(e){
    if(e==1){
      this.props.history.push('/deal/record')
    }else{
      this.props.history.push('/deal/transfer')
    }

  }
  componentWillReceiveProps(nextProps) {}


  render() {
    return (
      <div className="del_contain">
        {this.props.type==0?
          <div className="exchange_head flex-column">
          <div className="exchange_num">{this.props.quotPrice?this.props.quotPrice:''}</div>
          <div className="exchange_prev">当前成交值</div>
          <div className="to_record1 flex-row-between">
            <div className="exchange_to_record" onClick={this.to_record.bind(this,1)}>查看兑换记录</div>
            <div className="exchange_prev1">我的汇积分 {this.props.hui_balance?this.props.hui_balance:''}</div>
          </div>
        </div>
          :
          <div className="exchange_head flex-column">
            <div className="exchange_num1">一个OEC积分可转换成 <span>{this.props.quotPrice?this.props.quotPrice:''}</span> 个汇积分</div>
            <div className="exchange_prev">汇积分可兑换等值的商品</div>
            <div className="to_record flex-row-between">
              <div className="exchange_to_record" onClick={this.to_record.bind(this,2)}>查看转让记录</div>
              <div className="exchange_prev1">持有OEC积分 {this.props.OEC_balance?this.props.OEC_balance:''}</div>
            </div>
          </div>
        }
      </div>

    )
  }
}

export default withRouter(Exchange)



