import React from 'react'
import ReactDom from 'react-dom'
import "babel-polyfill"
import './static/css/index.css'


import { BrowserRouter,HashRouter, Route, Link, Switch, Redirect, browserHistory } from 'react-router-dom'

import {Provider} from 'mobx-react'
import RootStore from './mobx/RootStore'

import Index from './containers/HomeIndex/index'
import Message from './containers/Message/index'
import UserInfo from './containers/UserInfo/index'
import BusinessLicense from './containers/BusinessLicense/index'
import BusinessOrder from './containers/BusinessOrder/index'
import WithdrawalsRecord from './containers/WithdrawalsRecord/index'
import CashWithdrawals from './containers/CashWithdrawals/index'
import PlatformWithdrawals from './containers/PlatformWithdrawals/index'
import Withdrawals from './containers/Withdrawals/index'
import TransferMoney from './containers/TransferMoney/index'
import Problem from './containers/Problem/index'
import FinancialManagement from './containers/FinancialManagement/index'
import Bill from './containers/Bill/index'
import ServiceFee from './containers/ServiceFee/index'
import DepositPayment from './containers/DepositPayment/index'
import PayComplet from './containers/PayComplet/index'
import CreditRating from './containers/CreditRating/index'
import CreditRecord from './containers/CreditRecord/index'
import CheckCode from './containers/CheckCode/index'
import Login from './components/Login/index'

//交易所
import dealCenterHome from './containers/dealCenter/Home/index'
import dealExchange from './containers/dealCenter/Exchange'
import dealExchangeDetail from './containers/dealCenter/ExchangeDetail'
import record from './containers/dealCenter/Record/Record'
import transfer from './containers/dealCenter/Record/transfer'
import ScoreDetail from './containers/ScoreDetail/index'


ReactDom.render(
  <Provider rootStore={new RootStore()}>
    <HashRouter>
      <Switch>
        <Route path='/user' exact component={Index}></Route>
        <Route path='/dealCenterHome' component={dealCenterHome}></Route>
        <Route path='/dealExchange' component={dealExchange}></Route>
        <Route path='/dealExchangeDetail' component={dealExchangeDetail}></Route>
        <Route path='/deal/record' component={record}></Route>
        <Route path='/deal/transfer' component={transfer}></Route>
        <Route path='/score_detail' component={ScoreDetail}></Route>
        <Route path='/user/message' component={Message}></Route>
        <Route path='/user/user_info' component={UserInfo}></Route>
        <Route path='/user/business_license' component={BusinessLicense}></Route>
        <Route path='/user/business_order' component={BusinessOrder}></Route>
        <Route path='/user/withdrawals_record' component={WithdrawalsRecord}></Route>
        <Route path='/user/cash_withdrawals' component={CashWithdrawals}></Route>
        <Route path='/user/platform_withdrawals' component={PlatformWithdrawals}></Route>
        <Route path='/user/withdrawals' component={Withdrawals}></Route>
        <Route path='/user/transfer_money' component={TransferMoney}></Route>
        <Route path='/user/financial_management' component={FinancialManagement}></Route>
        <Route path='/problem' component={Problem}></Route>
        <Route path='/user/bill' component={Bill}></Route>
        <Route path='/user/service_fee' component={ServiceFee}></Route>
        <Route path='/user/deposit_payment' component={DepositPayment}></Route>
        <Route path='/user/pay_complet' component={PayComplet}></Route>
        <Route path='/user/credit_rating' component={CreditRating}></Route>
        <Route path='/user/credit_record' component={CreditRecord}></Route>
        <Route path='/user/check_code' component={CheckCode}></Route>
        <Route path='/login' component={Login}></Route>
        <Redirect to='/user'></Redirect>
      </Switch>
    </HashRouter>
  </Provider>,
  document.getElementById('root')
)



