import React from 'react';
import {withRouter} from 'react-router-dom'
import DocumentTitle from "react-document-title";
import {AjaxSend} from "../../util/util"
import {Toast} from 'antd-mobile'
import right from './right@2x.png'
import './css.css'



class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      info:{}
    }
  }
  componentDidMount() {
    let _this = this;
    const data = {
      _cmd: 'app_index',
    };
    AjaxSend(data, (da) => {
      _this.setState({
        info : da.data.info,
      });
    }, (e) => {
      Toast.fail(e);
    })
  }
  render() {
    return (
      <div className="user_cont">
        <DocumentTitle title="基本信息"/>
        <div className="list">
          <div className="list_item flex-row-between">
            <div className="list_item_title1">商家ID</div>
            <div className="list_item_title2">{this.state.info.id}</div>
          </div>
          <div className="list_item flex-row-between">
            <div className="list_item_title1">商家店铺名称</div>
            <div className="list_item_title2">{this.state.info.merchant_name}</div>
          </div>
          <div className="list_item flex-row-between">
            <div className="list_item_title1">商家店铺地址</div>
            <div className="list_item_title2">{this.state.info.merchant_address}</div>
          </div>
          <div className="list_item flex-row-between">
            <div className="list_item_title1">商家联系人</div>
            <div className="list_item_title2">{this.state.info.corpman_name}</div>
          </div>
          <div className="list_item flex-row-between">
            <div className="list_item_title1">商家手机号码</div>
            <div className="list_item_title2">{this.state.info.corpman_mobile}</div>
          </div>
          <div className="list_item flex-row-between">
            <div className="list_item_title1">让利折扣</div>
            <div className="list_item_title2">{JSON.stringify(parseFloat(this.state.info.benefit)/100)}</div>
          </div>
          <div className="list_item flex-row-between">
            <div className="list_item_title1">所属代理商</div>
            <div className="list_item_title2">{this.state.info.agent_name}</div>
          </div>
          <div className="list_item flex-row-between">
            <div className="list_item_title1">所属运营中心</div>
            <div className="list_item_title2">{this.state.info.operate_name}</div>
          </div>
          <div className="list_item flex-row-between" onClick={()=>this.props.history.push('/user/business_license')}>
            <div className="list_item_title1">营业执照</div>
            <img src={right} alt="" className="right_img"/>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(HomeIndex)




