import React from 'react'
import {Toast,Modal} from 'antd-mobile';
import  {AjaxSend,getCookie} from "../../../../../util/util";
import {withRouter} from 'react-router-dom'
import styles from './css.css'
// import TDImg from './img/icon01@2x.png'
const aler = Modal.alert;

class SubmitOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      pwd:'',
      pay_click:false,
    };
  }

  componentDidMount() {

  }

  componentWillReceiveProps(nextProps) {

  }

  close_show(){
    this.props.show_pwd(false)
  }
  pay(){
    if(this.state.pay_click){
      return
    }
    this.setState({
      pay_click:true
    })
    this.setState({
      show:true
    })
    var payData = {
      _cmd: 'product_order_create',
      pay_pwd: this.state.pwd,
    }
    var regAAA = new RegExp(/^\d{6}$/);
    if (!regAAA.test(this.state.pwd)) {
      Toast.fail('密码格式不正确',2)
      this.setState({
        pay_click:false
      })
      return
    }
    console.log(payData)
    var _this = this;


    //兑换转让请求:1兑换,2转让
    var data = {
      _cmd: 'oec_user_order',
      bsFlag:this.props.type,
      orderPrice:this.props.price,
      orderQty:this.props.num,
      orderType:1,
      contractType:1,
      userType:1,
      pay_password:this.state.pwd,
      pay_click:false
    }


    AjaxSend(data, (da) => {

      //if(_this.props.type==1){
        Toast.success('提交成功',2)
      //}
      setTimeout(()=>{
        if(_this.props.type==1){
          _this.props.history.push('/deal/record')
        }else{
          _this.props.history.push('/deal/transfer')
        }
      },2000)
      return
    }, (errors) => {
      _this.setState({
        pay_click:false
      })
      alert(errors);
    })
  }
  pwd(e){
    this.setState({
      pwd:e.target.value
    })
    this.props.pwd_num(e.target.value)
  }

  render() {

    return (
      <div className="ex_pwd">
        <div className="bla_mask" onClick={this.close_show.bind(this)}></div>
        <div className="pwd_cont flex-column">
          <div className="pwd_header">
            请输入您的支付密码
          </div>
          <div className="pwd_tip_cont">
            <div className="tip_text">您本次共{this.props.type==1?'兑换':'转让'}<span className="red"> {this.props.num}个OEC积分</span></div>
          </div>
          <div className="pwd_btn flex-row-between">
            <input type="password" placeholder="请输入支付密码" onChange={this.pwd.bind(this)}/>
            {this.state.pay_click?<div>支付</div>:<div onClick={this.pay.bind(this)}>支付</div>}
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(SubmitOrder)


