import React from 'react';
import {withRouter} from 'react-router-dom'
import './css.css'
import {to_fixed4} from '../../../../util/util'
class HomeWallet extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {

  }
  toReocrd1(){
    var data = {type:11};
    var path = {
      pathname:'/score_detail',
      state:data,
    }
    this.props.history.push(path);
  }

  toReocrd2(){
    var data = {type:10};
    var path = {
      pathname:'/score_detail',
      state:data,
    }
    this.props.history.push(path);
  }
  render() {
    let wallet = this.props.wallet
    return (
      <div className="home_wallet">
        <div className="wallet flex-row-around">
          <div className="walletLeft flex-column" onClick={this.toReocrd1.bind(this)}>
            <span className="walletCash">{wallet!={}&&parseFloat(wallet.hjf)?to_fixed4(wallet.hjf):''}</span>
            <span className="walletTitle">汇积分</span>
          </div>
          <div className="walletRight flex-column" onClick={this.toReocrd2.bind(this)}>
            <span className="walletCash">{wallet!={}&&parseFloat(wallet.hjf)?to_fixed4(wallet.oec):''}</span>
            <span className="walletTitle">OEC积分</span>
          </div>
        </div>
      </div>
    );
  }
}
export default withRouter(HomeWallet)



