import React from 'react'
import {Toast} from 'antd-mobile';
import {AjaxSend, to_fixed4} from '../../util/util'
import styles from './score_detail.css'
import TDImg from './img/icon01@2x.png'
import huiImg from './img/icon02@2x.png'
import background from './img/background@2x.png'
import List from './list'

class ScoreDetail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      type:0,
      list:[],
      total:'',
      freeze_value:'',
      oecAddress:'',
    };
  }

  componentDidMount() {
    var type=this.props.location.state.type;
    // var type=1;
    this.setState({
      type:type
    })

    const data = {
      _cmd: 'app_wallet_transfer_list',
      purse_type:type,
      option_type:1,
    };
    AjaxSend(data, (da) => {
      this.setState({
        total:parseFloat(da.data.wallet.balance)>=0?to_fixed4(parseFloat(da.data.wallet.balance)):0,
        freeze_value:to_fixed4(parseFloat(da.data.wallet.freeze_value))>=0?to_fixed4(parseFloat(da.data.wallet.freeze_value)):0,
      })
    }, (errors) => {
      Toast.fail(errors);
    })
    const dat = {
      _cmd: 'oec_user_account_oecAddress',
      currencyType:102,
    };
    AjaxSend(dat, (da) => {
      this.setState({
        oecAddress:da.data.oecAddress,
      })
    }, (errors) => {
      Toast.fail(errors);
    })
  }

  componentWillReceiveProps(nextProps) {

  }

  to_hui(){
      // Toast.info('转让功能内测中', 2)
      // return false
    this.props.history.push('/dealExchange');
  }
  to_td_shop(){
      var data = {type:1};
      var path = {
        pathname:'/oec_shop',
        state:data,
      }
      this.props.history.push(path);
  }
  to_exchange_record(){
      var data = {type:1};
      var path = {
        pathname:'/exchange_record',
        state:data,
      }
      this.props.history.push(path);
  }
  render() {
    return (
      <div className="score_detail">
        <div className="score_header flex-column">
          <img src={this.state.type==10?TDImg:huiImg} alt="" className={this.state.type==11?"img1":"img2"}/>
          <div className="score_header_title1">{this.state.type==10?"OEC积分":"汇积分"}</div>
          <div className="score_header_title2">{this.state.total} {this.state.type==0?<span style={{fontSize:'.26rem'}}>(待兑换数量约: {this.state.freeze_value})</span>:''}</div>
          {this.state.type==11?
            ''
            :<div className="score_header_title3" onClick={this.to_hui.bind(this)}>转让</div>
          }

        </div>
        {this.state.type==10?<div className="oec_address score_header_title1">我的OEC地址: {this.state.oecAddress}</div>:''}
        <List type={this.props.location.state.type}/>
      </div>
    )
  }
}

export default ScoreDetail


