import React from 'react';
import DocumentTitle from "react-document-title";
import ReactDom from 'react-dom'
import { PullToRefresh ,Toast} from 'antd-mobile'
import {AjaxSend} from "../../util/util"
import './css.css'



class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      list:[],
      refreshing: false,
      page : 1,
      no_data_show:false,
      not_more:false,
      height: document.documentElement.clientHeight,
    }
  }
  componentDidMount() {
    const height = this.state.height - ReactDom.findDOMNode(this.ptr).offsetTop;
    setTimeout(() => this.setState({
      height: height,
    }), 0);
    setTimeout(() => this.setState({
      no_data_show: true,
    }), 200);
    this.pageList()
  }
  pageList=(params)=>{
    let _this = this
    Toast.loading('Loading...', 30, () => {});
    const data = {
      _cmd:'app_withdraw_history',
    }
    let total_list = [];
    if (params == undefined) {
      total_list = this.state.list;
      data.page = this.state.page;
    }else{
      data.page = 1;
      this.setState({ page: 1})
    }
    AjaxSend(data, (da) => {
      let data = da.data.list.data;
      for (let i in data) {
        let list = [];
        list = data[i];
        total_list.push(list);
      }
      Toast.hide();
      if(this.state.list.length>0 && da.data.list.data.length==0){
        this.setState({
          not_more : true,
        });
        return
      }
      _this.setState({
        list : total_list,
      });
    }, (e) => {
      alert(e);
    })
  }
  onRefresh= ()=>{
    this.setState({ refreshing: true });
    this.setState({
      refreshing: false,
      page : this.state.page+1
    });
    this.pageList();
  }
  render() {
    let _this = this
    let ListItem = this.state.list.length>0?this.state.list.map((item,index)=>(
      <div className="list_item flex-column" key={index}>
        <div className="item flex-row-between">
          <div className="item_text1">{item.type==0?'高汇通提现':'收益提现'}</div>
          <div className="item_text2">￥{item.amount}</div>
        </div>
        <div className="item1 flex-row-between">
          <div className="item_text3">{item.created_at}</div>
          <div className={item.status==0?'item_text4':(item.status==1?'item_text4 act2':item.status==2?'item_text4 act1':'')}>{item.status==0?'提现中':(item.status==1?'提现成功':item.status==2?'提现失败':'')}</div>
        </div>
      </div>
    )): this.state.no_data_show?<div className="no_data">暂无更多</div>:''
    return (
      <div className="withdrawals_record">
        <DocumentTitle title="提现记录"/>
        <div className="tip">如若提现后未到账或有疑问，请联系18675530998</div>
        <div className="list">
          <PullToRefresh
            ref={el => this.ptr = el}
            style={{
              height: this.state.height,
              overflow: 'auto',
            }}
            indicator={{ deactivate: '上拉可以刷新' }}
            direction={this.state.list.length>0?'up':'down'}
            refreshing={this.state.refreshing}
            onRefresh={this.onRefresh}
          >
            {ListItem}
            {this.state.not_more?<div className="not_more">已经到底部了哦~</div>:this.state.list.length>0?<div className="not_more">上拉加载更多</div>:''}
          </PullToRefresh>
        </div>

      </div>
    );
  }
}

export default HomeIndex




