import React from 'react'
import styles from './css.css'
import icon_dh from './img/icon_dh@2x.png'
import icon_zr from './img/icon_zr@2x.png'
import {Toast} from 'antd-mobile'
import {AjaxSend,to_fixed4,to_fixed2} from '../../../../../util/util'


class Download extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      spinnerShow: false,
      listData:[],
      timer:''
    };
  }

  componentDidMount() {
    this.getListData()
    this.time()
  }

  componentWillReceiveProps(nextProps) {

  }

  //获取列表数据
  getListData(){
    var _that=this;
    const data = {
      _cmd: 'oec_quot_orders'
    };
    AjaxSend(data, (da) => {
        var data = da.data[1];
        if (!data) {
          data = {};
          data.buyPrice = {};
          data.buyQty = {};
          data.sellPrice = {};
          data.sellQty = {};
        }
        var sell = {
          type: 3,
          data: (function () {
            var arr = [];
            for (var i = 0; i < data.sellPrice.length; i++) {
              arr.push({
                bsFlag: 2,
                Price: data.sellPrice[i],
                Qty: data.sellQty[i]
              });
              if (i >= 12) {
                break
              }
            }
            return arr.reverse();
          })()
        };
        var buy = {
          type: 2,
          len: 0,
          data: (function () {
            var arr = [];
            for (var i = 0; i < data.buyPrice.length; i++) {
              arr.push({
                bsFlag: 1,
                Price: data.buyPrice[i],
                Qty: data.buyQty[i]
              });
              if (i >= 12) {
                break
              }
            }
            return arr;
          })()
        };
        var all = {
          type: 1,
          len: sell.data.length > 6 ? 6 : sell.data.length,
          data: (function () {
            if (sell.data.length < 6 && buy.data.length < 6) {
              return sell.data.concat(buy.data);
            } else if (sell.data.length < 6) {
              return sell.data.concat(buy.data.slice(0, 12 - sell.data.length));
            } else if (buy.data.length < 5) {
              return sell.data.slice(-12 + buy.data.length, sell.data.length).concat(buy.data);
            } else {
              return sell.data.slice(sell.data.length - 6, sell.data.length).concat(buy.data.slice(0, 6));
            }
          })()
        };
        sell.data = sell.data.slice(-13, sell.data.length);
        _that.setState({
          listData:all.data,
          spinnerShow:false
        })
        console.log(_that.state.listData)

    },(err)=>{
      Toast.info(err,2);
    })
  }
  time(){
    let self_=this;
    this.state.timer=setInterval(()=>{
      self_.getListData();
    },6000)
  }
  componentWillUnmount(){
    clearInterval(this.state.timer)
  }
  render() {
    return (
      <div className="zz_list1">
        {this.state.listData.length>0?this.state.listData.map((item,index)=>(
          <div className="zz_list flex-row-between" key={index}>
            {item.bsFlag==2?<img src={icon_zr} alt="" className="icon2"/>:<img src={icon_dh} alt="" className="icon1"/>}
            <div className="zz_num1">{item.bsFlag==2?'转让值':'兑换值'} {to_fixed4(item.Price)}</div>
            <span>|</span>
            <div className="zz_num2">{item.bsFlag==2?'转让数量':'兑换数量'} {to_fixed4(item.Qty)}</div>
          </div>
        )): <div className="no_data">暂无更多</div> }
      </div>
    )
  }
}


export default Download


