import React from 'react';
import DocumentTitle from "react-document-title";
import ReactDom from 'react-dom'
import { PullToRefresh ,Toast} from 'antd-mobile'
import {AjaxSend,to_fixed4} from "../../util/util"
import './css.css'
import dataImg  from './img/date@2x.png'
import numImg  from './img/num@2x.png'
import timeImg  from './img/time@2x.png'
import state1Img  from './img/state1@2x.png'
import state2Img  from './img/state2@2x.png'
import state3Img  from './img/state3@2x.png'



class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      list:[],
      refreshing: false,
      page : 1,
      no_data_show:false,
      not_more:false,
      height: document.documentElement.clientHeight,
    }
  }
  componentDidMount() {
    const height = this.state.height - ReactDom.findDOMNode(this.ptr).offsetTop;
    setTimeout(() => this.setState({
      height: height,
    }), 0);
    setTimeout(() => this.setState({
      no_data_show: true,
    }), 200);
    this.pageList()
  }
  pageList=(params)=>{
    let _this = this
    Toast.loading('Loading...', 30, () => {});
    const data = {
      _cmd:'app_subsidy_bill',
    }
    let total_list = [];
    if (params == undefined) {
      total_list = this.state.list;
      data.page = this.state.page;
    }else{
      data.page = 1;
      this.setState({ page: 1})
    }
    AjaxSend(data, (da) => {
      let data = da.data.list.data;
      for (let i in data) {
        let list = [];
        list = data[i];
        total_list.push(list);
      }
      Toast.hide();
      if(this.state.list.length>0 && da.data.list.data.length==0){
        this.setState({
          not_more : true,
        });
        return
      }
      _this.setState({
        list : total_list,
      });
      console.log(this.state.list)
    }, (e) => {
      alert(e);
    })
  }
  onRefresh= ()=>{
    this.setState({ refreshing: true });
    this.setState({
      refreshing: false,
      page : this.state.page+1
    });
    this.pageList();
  }
  render() {
    let ListItem = this.state.list.length>0?this.state.list.map((item,index)=>(
      <div className="list_item flex-row-between" key={index}>
        <img src={item.status==1?state1Img:(item.status==2?state2Img:state3Img)} alt="" className="state_img"/>
        <div className="list_item_right flex-column">
            <div className="list_item_title">补贴总额度 : {to_fixed4(item.amount)}</div>
            <div className="flex-row-between">
              <div className="title_cont flex-row-between">
                <img src={dataImg} alt="" className="dataImg"/>
                <div className="list_item_text1">已补贴{item.finish_day}天</div>
              </div>
              <div className="time_cont1 flex-row-between">
                <img src={numImg} alt="" className="dataImg"/>
                <div className="list_item_num">对账单编号{item.id}</div>
              </div>
            </div>
            <div className="time_cont flex-row-between">
              <img src={timeImg} alt="" className="dataImg"/>
              <div className="list_item_time">{item.created_at}</div>
            </div>
        </div>
      </div>
    )): this.state.no_data_show?<div className="no_data">暂无更多</div>:''
    return (
      <div className="bill_cont">
        <DocumentTitle title="补贴对账单"/>
        <div className="list">
          <PullToRefresh
            ref={el => this.ptr = el}
            style={{
              height: this.state.height,
              overflow: 'auto',
            }}
            indicator={{ deactivate: '上拉可以刷新' }}
            direction={this.state.list.length>0?'up':'down'}
            refreshing={this.state.refreshing}
            onRefresh={this.onRefresh}
          >
            {ListItem}
            {this.state.not_more?<div className="not_more">已经到底部了哦~</div>:this.state.list.length>10?<div className="not_more">上拉加载更多</div>:''}
          </PullToRefresh>
        </div>

      </div>
    );
  }
}

export default HomeIndex




