import React from 'react';
import {withRouter} from 'react-router-dom'
import DocumentTitle from "react-document-title";
import { List } from 'antd-mobile';
import depositImg from './img/deposit@2x.png'
import problemImg from './img/problem@2x.png'
import './css.css'

const Item = List.Item;


class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={

    }
  }
  componentDidMount() {

  }
  render() {
    return (
      <div className="platform_withdrawals">
        <DocumentTitle title="技术服务费"/>
        <div className="header">
          <div className="money_text">账户余额（元）</div>
          <div className="money_num">10000000.00</div>
        </div>
        <List>
          <Item
            thumb={depositImg}
            arrow="horizontal"
            onClick={() => {this.props.history.push('/user/deposit_payment')}}
          >预存</Item>
        </List>
        <div className="to_tip flex-row-between" onClick={() => {this.props.history.push('/problem')}}>
          <img src={problemImg} alt="" className="problemImg"/>
          <div className="pro_text">常见问题</div>
        </div>
      </div>
    );
  }
}

export default withRouter(HomeIndex)




