import React from 'react';
import DocumentTitle from "react-document-title";
import { Toast} from 'antd-mobile'
import {AjaxSend,to_fixed2} from "../../util/util"
import cardImg from './img/card@2x.png'
import arrowImg from './img/right@2x.png'
import './css.css'
import PriceForm from './subpage/CashForm'



class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      mobile:'',
      balance:'',
      price:'',
      count: 60,
      send: true,
      timer:''
    }
  }
  componentDidMount() {
    let _this = this
    const data = {
      _cmd:'app_withdraw_reward',
    }
    AjaxSend(data, (da) => {
      _this.setState({
        balance : to_fixed2(da.data.balance),
      });
    }, (e) => {
      Toast.fail(e);
    })
  }
  set_mobile(e){
    this.setState({
      mobile:e
    })
  }
  set_price(e){
    this.setState({
      price:e
    })
  }
  commit(){
    function Trim(str) {
      return str.replace(/\s+/g, "");
    }
    if(parseFloat(this.state.price)>this.state.balance){
      Toast.info('余额不足')
      return
    }
    if(this.state.price<5){
      Toast.info('提现金额最少5元')
      return
    }
    if(this.state.price>50000){
      Toast.info('提现金额最多50000元')
      return
    }
    let mobile = Trim(JSON.stringify(this.state.mobile))
    if ((/^1[34578]\d{9}$/.test(mobile))) {
      Toast.info('手机号码有误')
      return
    }

    const data = {
      _cmd:'app_rewardToConsumption',
      amount:this.state.price,
      mobile:JSON.parse(mobile),
    }
    AjaxSend(data, (da) => {
      Toast.success('申请成功')
    }, (e) => {
      Toast.fail(e);
    })
  }
  send_click() {
      // const data = {
      //   _cmd: 'app_send_login_msg',
      //   mobile: this.state.user_num,
      // };
      if (this.state.send) {
      //   AjaxSend(data, (da) => {
      //     console.log(da.status)
          // this.setState({status: da.status})
          console.log(this)
          this.timer = setInterval(function () {
            var count = this.state.count;
            this.state.send = false;
            count -= 1;
            if (count < 1) {
              this.setState({
                send: true
              });
              count = 60;
              clearInterval(this.timer);
            }
            this.setState({
              count: count
            });
          }.bind(this), 1000);
      //
      //   }, (errors) => {
      //     Toast.fail(errors, 1);
      //   });
      } else {
        Toast.info('已发送', 1)
      }
  }

  render() {
    var send_text = this.state.send ? '点击获取' : this.state.count + '秒重新发送';
    return (
      <div className="transfer_money">
        <DocumentTitle title="转账至消费额"/>
        <PriceForm  balance={this.state.balance} price={this.state.price} mobile={this.state.mobile} set_price={this.set_price.bind(this)} set_mobile={this.set_mobile.bind(this)}/>
        <div className="phone_cont flex-row-between">
            <div className="phone_title">手机号:</div>
            <div className="phone_num">{'13576790866'}</div>
        </div>
        <div className="inputCont">
          <input type="text" placeholder="请输入手机验证码" className="userInput" onChange={this.get_code_tip} />
          <span className="sendCode" onClick={this.send_click.bind(this)}>{send_text}</span>
        </div>
        <ul>
          <li className="tip">1.每日最多可转出100笔 ;</li>
          <li className="tip">2.每笔转出服务费用为1.5元，单笔转出上限为50000元。如：单笔转出了50000元，实到转出用户的消费额金额为49998.5元。</li>
          <li className="tip">3.D0即时到账(可能会碰到1-2分钟的延迟),节假日也包括在内。</li>
          <li className="tip">4.转出的用户必须是在当前商家消费过的用户，非消费过的用户不可转出。</li>
        </ul>
        <div className="b_height"></div>
        <div className="btn" onClick={this.commit.bind(this)}>确认转账</div>
      </div>
    );
  }
}

export default HomeIndex




