import React from 'react';
import {withRouter} from 'react-router-dom'
import './css.css'
import message from '../../img/message@2x.png'
import score from '../../img/jifen@2x.png'



class HomeHeader extends React.Component{
  constructor(props) {
    super(props);
    this.state={}
  }
  componentDidMount() {

  }
  render() {
    return (
      <div className="home_header flex-row-between">
        <img src={this.props.info.logo} alt="" className="userPhoto"/>
        <div className="user flex-column">
          <span className="name">{this.props.info.corpman_name}</span>
          <span className="mobile">手机号 : {this.props.info.corpman_mobile}</span>
        </div>
        <div className="score flex-column">
          <img src={score} alt="" className="scoreImg"/>
          <span className="scoreText">信用评分{this.props.credit_score}</span>
        </div>
        <div className="headerRight" onClick={()=>this.props.history.push('/user/message')}>
          <img src={message} alt="" className="message"/>
          {/*<span className="messageNum">1</span>*/}
        </div>
      </div>
    );
  }
}
export default withRouter(HomeHeader)


