import React from 'react';
import DocumentTitle from "react-document-title";
import { List, Checkbox } from 'antd-mobile';
import './css.css'
import wxImg from './img/weixin@2x.png'
import bankImg from './img/unionpay@2x.png'
import PriceForm from './subpage/CashForm'
const CheckboxItem = Checkbox.CheckboxItem;
const Item = List.Item;
const Brief = Item.Brief;

class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      list:[
        {
          id:'1',
          icon:wxImg,
          pay_name:'微信支付',
          text:'微信安全支付',
          disabled:false,
          checked:false,
          amount:'-1'
        }
      ],
    }
  }
  componentDidMount() {

  }
  onChange = (id,index,e) => {
    var _this = this;
    console.log(id,index,e);
    console.log(e.target.checked);
    let a = this.state.list
    if(e.target.checked) {
      for (var i = 0; i < a.length; i++) {
        a[i].checked = true;
        a[i].disabled = false;
      }
      console.log(a)
      this.setState({
        list:a
      })
      return
    }else{
      for (var i = 0; i < a.length; i++) {
        a[i].checked = false;
        a[i].disabled = false;
      }
      this.setState({
        list:a
      })
      return
    }
  }
  render() {
    const arr = this.state.list
    return (
      <div className="transfer_money">
        <DocumentTitle title="预存支付"/>
        <PriceForm />
        <List renderHeader={() => '选择支付方式'}>
          {arr!=[]?arr.map((i,m) => (
            <CheckboxItem key={i.id} disabled={i.disabled} checked={i.checked} onChange={this.onChange.bind(this,i.id,m)}>
              <div className="pay_cont flex-row-between" style={{color:i.disabled?'#ddd':'#333'}}>
                <img src={i.icon} alt=""/>
                <div className="title_cont flex-column">
                  <div className="title_text1">{i.pay_name}</div>
                  <div className="title_text2">{i.text}</div>
                  </div>
                {i.amount!=-1?
                  <div className="title1">
                    余额：￥{i.amount.toFixed(2)}元
                  </div>:''
                }
              </div>

            </CheckboxItem>
          )):''}
        </List>
        <List>
          <Item
            arrow="horizontal"
            thumb={bankImg}
            multipleLine
            onClick={() => {}}
          >
            银联支付 <Brief>中国银联在线支付</Brief>
          </Item>
      </List>
        {/*<ul>*/}
          {/*<li className="tip">1.每日最多可转出100笔 ;</li>*/}
        {/*</ul>*/}
        <div className="btn">确认支付</div>
      </div>
    );
  }
}

export default HomeIndex




