import React from 'react';
import {withRouter} from 'react-router-dom'
import DocumentTitle from "react-document-title";
import { List ,Toast} from 'antd-mobile';
import {AjaxSend,to_fixed2} from "../../util/util"
import withdrawalsImg from './img/cash@2x.png'
import transferImg from './img/transfer@2x.png'
import problemImg from './img/problem@2x.png'
import './css.css'

const Item = List.Item;


class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      total:'',
    }
  }
  componentDidMount() {
    let _this = this
    const data = {
      _cmd:'app_index',
    }
    AjaxSend(data, (da) => {
      _this.setState({
        total:to_fixed2(da.data.wallet.reward)
      })
    }, (e) => {
      Toast.fail(e);
    })
  }
  render() {
    return (
      <div className="platform_withdrawals">
        <DocumentTitle title="平台交易提现"/>
        <div className="header">
          <div className="money_text">账户余额（元）</div>
          <div className="money_num">{this.state.total}</div>
        </div>
        <List>
          <Item
            thumb={withdrawalsImg}
            arrow="horizontal"
            onClick={() => {this.props.history.push('/user/withdrawals')}}
          >提现</Item>
          <Item
            thumb={transferImg}
            onClick={() => {this.props.history.push('/user/transfer_money')}}
            arrow="horizontal"
          >
            转账至消费额
          </Item>
        </List>
        <div className="to_tip flex-row-between" onClick={() => {this.props.history.push('/problem')}}>
          <img src={problemImg} alt="" className="problemImg"/>
          <div className="pro_text">常见问题</div>
        </div>
      </div>
    );
  }
}

export default withRouter(HomeIndex)




