import React from 'react'
import ScoreCss from './css/about_us.css'
import { Accordion } from 'antd-mobile';

class AboutUs extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        };
    }
    onChange = (key) => {

    }

    componentDidMount() {

    }

    componentWillReceiveProps(nextProps) {

    }


    render() {

        return (
            <div style={{ marginBottom: 10 }}>
                <Accordion accordion openAnimation={{}} className="my-accordion" onChange={this.onChange}>
                    <Accordion.Panel header="1.中商共享是什么？" className="pad">
                        中商共享经济发展(深圳)有限公司（简称中商共享），由中投实业投资发展深圳有限公司、乾鼎国际控股集团、深圳十八购科技有限公司共同控股，致力培育民族产业、塑造民族品牌、促进经济转型升级和新型产业发展。投资及孵化产业涵盖首饰珠宝、汽车游艇、信息科技、生物医药、零售服务、房地产等国家政策重点扶持的行业领域。<br/>
                        中商共享运营中商共享消费平台，通过消费资本运作，为消费者提供优质购物体验的同时实现消费增值。通过消费所产生的现金流作为金融杠杆驱动，孵化旗下投资产业，实现资本增值。
                    </Accordion.Panel>
                    <Accordion.Panel header="2.什么是“项目合伙人”？" className="pad">
                        中商共享消费平台设立项目合伙人，仅限100名。根据中商共享平台零售业绩的一定比例设立分红奖金池。项目合伙人可享受平均分红奖金池利润收益，收益每三天分配一次。
                    </Accordion.Panel>
                </Accordion>
            </div>
        )
    }
}


export default AboutUs


