import React from 'react'
import { List, InputItem } from 'antd-mobile';
import css from './css.css'
import {to_fixed4,to_fixed2} from '../../../../../util/util'

class ExchangeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    };
  }

  componentDidMount() {

  }



  componentWillReceiveProps(nextProps) {}
  clearNoNum (obj) {
    let self_ = this;
    obj = obj.replace(/[^\d.]/g, ""); //清除"数字"和"."以外的字符
    obj = obj.replace(/^\./g, ""); //验证第一个字符是数字而不是
    obj = obj.replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的
    obj = obj.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
    obj = obj.replace(/^(\-)*(\d+)\.(\d\d\d\d).*$/, '$1$2.$3'); //只能输入4个小数
    if (obj.indexOf(".") < 0 && obj != "") {
      if (obj.substr(0, 1) == '0' && obj.length == 2) {
        obj = obj.substr(1, obj.length);
      }
        console.log(obj)
    }

      return obj
    // self_.changePrice = obj;
    // self_.consumeIntegral = parseFloat(self_.changePrice * self_.changeNum);

  }
  clearNoNum2 (obj) {
    let self_ = this;
    obj = obj.replace(/[^\d.]/g, ""); //清除"数字"和"."以外的字符
    obj = obj.replace(/^\./g, ""); //验证第一个字符是数字而不是
    obj = obj.replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的
    obj = obj.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
    obj = obj.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入4个小数
    if (obj.indexOf(".") < 0 && obj != "") {
      if (obj.substr(0, 1) == '0' && obj.length == 2) {
        obj = obj.substr(1, obj.length);
      }

    }
    if(obj.indexOf(".") < 0&&obj.length > 6){
      obj = obj.slice(0, 6)
    }
    return obj

  }
  current_value1(){
    var a = this.props.quotPrice
    a = this.clearNoNum(a)
    this.props.set_price(a)
  }

  current_value2(){
    var b = this.props.price
    if(this.props.type==0){
      var a = to_fixed2(this.props.hui_balance/b)
      this.props.set_num(a)
    } else{
      var b = to_fixed2(this.props.OEC_balance)
      this.props.set_num(b)
    }
  }
  set_price1(e){
    var a=  this.clearNoNum(e)
    this.props.set_price(a)
  }
  set_num1(e){
      var a=  this.clearNoNum2(e)
    this.props.set_num(a)
  }
  to_fix4(e){
    if(e!=''){
        this.props.set_price(to_fixed4(e))
    }

  }
  to_fix2(e){
    if(e!=''){
      this.props.set_num(to_fixed2(e))
    }
  }

  render() {
    return (
      <div className="deal_exchange_form">
        <List>
          <InputItem
            placeholder=""
            value={this.props.price}
            type="money"
            moneyKeyboardAlign="left"
            onChange={this.set_price1.bind(this)}
            onBlur={this.to_fix4.bind(this)}
            extra={<div className="current_value2_cont flex-row-between"><span>|</span><div className='current_value1' onClick={this.current_value1.bind(this)}>{this.props.type==0?'填入当前兑换值':'填入当前转让值'}</div></div>}
          >{this.props.type==0?'兑换值':'转让值'}</InputItem>
          <InputItem
            placeholder=""
            type="money"
            moneyKeyboardAlign="left"
            value={this.props.num}
            onChange={this.set_num1.bind(this)}
            onBlur={this.to_fix2.bind(this)}
            extra={<div className="current_value2_cont flex-row-between"><span>|</span><div className='current_value2' onClick={this.current_value2.bind(this)}>{this.props.type==0?'全部兑换':'全部转让'}</div></div> }
          >{this.props.type==0?'兑换个数':'转让个数'}</InputItem>
        </List>
      </div>
    )
  }
}

export default ExchangeForm




