import React from 'react';
import DocumentTitle from "react-document-title";
import './css.css'
import { List } from 'antd-mobile';
import successImg from './img/success@2x.png'
import failImg from './img/fail@2x.png'
import {AjaxSend} from "../../util/util"

const Item = List.Item;

class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={

    }
  }
  componentDidMount() {
    let data = {
      _cmd:'app_messages'
    }
    AjaxSend(data,(da)=>{
        console.log(da)
    },(err)=>{

    })
  }
  render() {
    return (
      <div className="pay_complet_cont">
        <DocumentTitle title="支付结果"/>
        <img src={successImg} alt="" className="header_img"/>
        <List className="my-list">
          <Item extra={'10000.00元'}>预存金额</Item>
          <Item extra={'支付成功'}>支付状态</Item>
        </List>
        <div className="btn">返回技术服务费首页</div>
      </div>
    );
  }
}

export default HomeIndex




