import React from 'react'
import logoImg from './img/logo@2x.png'
import css from './css/login.css'
import { AjaxSend, setCookie, getCookie ,code_img} from '../../util/util'
import { Toast } from 'antd-mobile';
// Checkbox
// let introducer = '';
// const CheckboxItem = Checkbox.CheckboxItem;
// const AgreeItem = Checkbox.AgreeItem;
var guid;

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 60,
            send: true,
            click_num:0,
            user_num: '',
            img_code: '',
            code_tip: '',
            status: '',
            img_code_src:'',
            guid:'',
            ticket: '',
            isagree: false,
            iskey:false,
            img_code_show:true
        };
        this.user_add_user = this.user_add_user.bind(this);
        this.send_click = this.send_click.bind(this);
        this.change_num = this.change_num.bind(this);
        this.get_code_tip = this.get_code_tip.bind(this);
        this.get_img_code = this.get_img_code.bind(this);
        this.img_code_click = this.img_code_click.bind(this);
    }

    componentDidMount() {
        guid = this.get_guid()
        this.setState({
            img_code_src:code_img()+guid,
            guid:guid
        })

    }
    componentWillMount() {

    }
    componentWillReceiveProps(nextProps) {

    }

    change_num(e) {
        var user_num = e.target.value;
        this.setState({ user_num: user_num })
    }

    get_code_tip(e) {
        var code_tip = e.target.value;
        this.setState({ code_tip: code_tip })
    }

    get_img_code(e) {
        var img_code = e.target.value;
        this.setState({ img_code: img_code })
    }

    img_code_click(){
        guid = this.get_guid()
        this.setState({
            img_code_src:code_img()+guid,
            guid:guid
        })
    }


    user_add_user() {
        let _this = this
        const data = {
            _cmd: 'app_app_login',
            mobile: this.state.user_num,
            message_code: this.state.code_tip,
            openid:getCookie('openid')?getCookie('openid'):'',
            unionid:getCookie('unionid')?getCookie('unionid'):'',
        };

        if ((/^1[34578]\d{9}$/.test(data.mobile))) {
            AjaxSend(data, (da) => {
                setCookie('ticket', da.data.ticket)
                Toast.success('登录成功，即将跳到首页', 2);
                setTimeout(function () {
                    _this.props.history.push('/user')
                }, 2000)
            }, (errors) => {
                Toast.fail(errors, 1)
            })
        }
        else {
            Toast.fail('请输入格式正确的手机号码或密码！', 1)
        }
    }

    S4() {
        return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    }

    get_guid() {
        return (this.S4() + this.S4() + "-" + this.S4() + "-" + this.S4() + "-" + this.S4() + "-" + this.S4() + this.S4() + this.S4());
    }


    send_click() {
        if (/^1[34578]\d{9}$/.test(this.state.user_num)) {
          if (this.state.img_code=='') {
            Toast.info('请先输入验证码！', 2)
            return
          }
          const data = {
            _cmd: 'app_send_login_msg',
            mobile: this.state.user_num,
            captcha: this.state.img_code,
            uuid: this.state.guid,
          };
            if (this.state.send) {
                AjaxSend(data, (da) => {
                    console.log(da.status)
                    this.setState({status: da.status})
                    this.timer = setInterval(function () {
                        var count = this.state.count;
                        this.state.send = false;
                        count -= 1;
                        if (count < 1) {
                            this.setState({
                                send: true
                            });
                            count = 60;
                            clearInterval(this.timer);
                        }
                        this.setState({
                            count: count
                        });
                    }.bind(this), 1000);

                }, (errors) => {
                    Toast.fail(errors, 1);
                });
            } else {
                Toast.info('已发送', 1)
            }
        }
        else {
            Toast.fail('请输入格式正确的手机号码！', 1)
        }

    }

    // onChange_Agree() {
    //     if (this.state.isagree) {
    //         this.setState({
    //             isagree: false
    //         });
    //     } else {
    //         this.setState({
    //             isagree: true
    //         });
    //     }
    // }

    render() {
        var send_text = this.state.send ? '点击获取' : this.state.count + '秒重新发送';
        return (
            <div className="login_cont">
                <div className="container flex-column">
                    <header>
                        <img src={logoImg} alt="logo" className="logoImg" />
                    </header>
                    <div className="inputCont">
                        <input type="text" placeholder="请输入您的手机号码" className="userInput" maxLength={11}
                               onChange={this.change_num} />
                        <span> </span>
                    </div>
                  {
                    this.state.img_code_show?<div className="code_item">
                        <input type="text" placeholder="请输入图形验证码" className="wx_userInput" onChange={this.get_img_code} />
                        <img className="img_code" src={this.state.img_code_src} onClick={this.img_code_click} />
                    </div>:''
                  }
                    <div className="inputCont">
                        <input type="text" placeholder="请输入手机验证码" className="userInput" onChange={this.get_code_tip} />
                        <span className="sendCode" onClick={this.send_click}>{send_text}</span>
                    </div>{/* e => console.log('checkbox', e) */}
                  {/*<div className="reg_agree">*/}
                  {/*<AgreeItem data-seed="logId" onChange={() => this.onChange_Agree()}>*/}
                  {/*请同意 <a onClick={(e) => {*/}
                  {/*e.preventDefault();*/}
                  {/*window.location.href = "/quan/_agreement/RegistrationProtocol.html";*/}

                  {/*}}>《平台服务协议》</a>*/}
                  {/*</AgreeItem>*/}
                  {/*</div>*/}
                  {/*{this.state.isagree ?*/}
                  {/*<div className="register" onClick={this.user_add_user}>绑定与注册</div> :*/}
                    <div className="register register_gray" onClick={this.user_add_user}>登录</div>
                  {/*}*/}


                  {/*<div className="to_login" onClick={() => {*/}
                  {/*window.location.href = '/login'*/}
                  {/*}}>已有账号?前往登录</div>*/}
                </div>
            </div>
        )
    }
}


export default Login


