import React from 'react';
import {withRouter} from 'react-router-dom'
import './css.css'
import {to_fixed2} from "../../../../util/util"
import message from '../../img/icon01@2x.png'
import user_info from '../../img/icon02@2x.png'
import seller_code from '../../img/icon03@2x.png'
import seller_order from '../../img/icon04@2x.png'
import seller_OEC from '../../img/icon05@2x.png'
import withdrawals_record from '../../img/icon06@2x.png'
import cash_withdrawals from '../../img/icon07@2x.png'
import forum_withdrawals from '../../img/icon08@2x.png'
import financial_management from '../../img/icon09@2x.png'
import service_fee from '../../img/icon10@2x.png'
import marketing_tools from '../../img/icon11@2x.png'


class HomeList extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      seller_OEC: 0,
      cash_withdrawals: 0,
      forum_withdrawals: 0,
      arr: [
        {
          img:message,
          title:'我的消息',
          tip:'',
          url: '/user/message',
        },{
          img:user_info,
          title:'基本信息',
          tip:'',
          url: '/user/user_info',
        },{
          img:seller_code,
          title:'商家买单码',
          tip:'',
          url: '/user/check_code',
        },{
          img:seller_order,
          title:'我的商家订单',
          tip:'',
          url: '/user/business_order',
        },{
          img:seller_OEC,
          title:'OEC流通',
          tip:'待确认: ',
          url: '/dealCenterHome',
        },{
          img:withdrawals_record,
          title:'提现记录',
          tip:'',
          url: '/user/withdrawals_record',
        },{
          img:cash_withdrawals,
          title:'现金交易提现',
          tip:'可提现金额: ',
          url: '/user/cash_withdrawals',
        },{
          img:forum_withdrawals,
          title:'平台交易提现',
          tip:'可提现金额: ',
          url: '/user/platform_withdrawals',
        },{
          img:financial_management,
          title:'财务管理',
          tip:'',
          url: '/user/financial_management',
        },{
          img:service_fee,
          title:'技术服务费预存',
          tip:'',
          url: '/user/service_fee',
        },{
          img:marketing_tools,
          title:'平台营销工具',
          tip:'',
          url: '',
        },
      ],
    }
  }

  componentDidMount() {

  }
  render() {
    let arr = this.state.arr;
    arr[4].tip = '待确认: '+this.props.oec_order_await_count;
    arr[6].tip = '可提现金额:¥ ' +(this.props.balance_ght);
    arr[7].tip = '可提现金额:¥ '+(this.props.reward);
    return (
      <div className="home_list">
        <div className="list flex-row-between">
          {arr.map((item,index)=> (
            <div className="listItem flex-column" key={index} onClick={()=>item.url!==''?this.props.history.push(item.url):''}>
              <img src={item.img} className="image" alt=""/>
                <span className="titles">{item.title}</span>
                {item.tip!==''?<span className="tip">{item.tip}</span>:''}
            </div>))}
        </div>
      </div>
    );
  }
}
export default withRouter(HomeList)



