import React from 'react';
import './css.css'
import banner from './img/banner_img@2x.png'
import HomeHeader from "./subpage/HomeHeader";
import HomeWallet from "./subpage/HomeWallet";
import HomeList from "./subpage/HomeList";
import DocumentTitle from "react-document-title";
import {AjaxSend,to_fixed2,setCookie} from "../../util/util"
import {Toast} from 'antd-mobile'


class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      info:{},
      wallet:{},
      balance_ght:'',//现金可提现
      reward:'',//平台交易可提现
      oec_order_await_count:'',//待确认
      credit_score:'',//信用评分
    }
  }
  componentDidMount() {
    let _this = this;
    const data = {
      _cmd: 'app_index',
    };
    AjaxSend(data, (da) => {
      _this.setState({
        info : da.data.info,
        oec_order_await_count : to_fixed2(da.data.oec_order_await_count),
        wallet : da.data.wallet,
        balance_ght : to_fixed2(da.data.balance_ght),
        reward : to_fixed2(da.data.wallet.reward),
        credit_score : da.data.credit_score,

      });
      setCookie('can_trade_oec',da.data.can_trade_oec)
    }, (e) => {
      Toast.fail(e);
    })
  }
  render() {
    return (
      <div className="home_index">
        <DocumentTitle title="商家主页"/>
        <HomeHeader info={this.state.info} credit_score={this.state.credit_score}/>
        <HomeWallet wallet={this.state.wallet}/>
        <HomeList balance_ght={this.state.balance_ght} reward={this.state.reward} oec_order_await_count={this.state.oec_order_await_count} />
        <img src={banner} alt="" className="banner_img"/>
      </div>
    );
  }
}

export default HomeIndex




