import React from 'react'
import ReactDom from 'react-dom'
import { PullToRefresh ,Toast} from 'antd-mobile'
import  {AjaxSend} from "../../../util/util";
import styles from './css.css'

class List extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      tab:['全部','收入','支出'],
      tab_index:0,
      list:[],
      type:this.props.type,
      refreshing: false,
      page : 1,
      no_data_show:false,
      not_more:false,
      height: document.documentElement.clientHeight,
    };
  }

  componentDidMount() {
    console.log(this.props.type)
    const height = this.state.height - ReactDom.findDOMNode(this.ptr).offsetTop;
    setTimeout(() => this.setState({
      height: height,
    }), 0);
    // setTimeout(() => this.setState({
    //   no_data_show: true,
    // }), 200);
    this.pageList(0)
  }

  componentWillReceiveProps(nextProps) {
    // console.log(nextProps)
  }

  pageList=(params)=>{
    console.log(params)
    let _this = this
    Toast.loading('加载中...', 30, () => {});
    const data = {
      _cmd:'app_wallet_transfer_list',
      purse_type:this.props.type,
    }
    let total_list = [];
    if (params == undefined) {
      total_list = this.state.list;
      data.page = this.state.page;
      data.option_type = this.state.tab_index+1;
    }else{
      data.page = 1;
      data.option_type = params+1;
      this.setState({ page: 1})
    }
    AjaxSend(data, (da) => {
      let data = da.data.list.data;
      for (let i in data) {
        let list = [];
        list = data[i];
        total_list.push(list);
      }
      Toast.hide();
      if(params != undefined && da.data.list.data.length==0){
        this.setState({
          no_data_show: true,
        });
        return
      }
      if(this.state.list.length>0 && da.data.list.data.length==0){
        this.setState({
          not_more : true,
        });
        return
      }
      _this.setState({
        list : total_list,
      });
      console.log(this.state.list)
    }, (e) => {
      Toast.fail(e);
    })
  }
  tab_item(index){
    if(this.state.tab_index!=index){
      this.setState({
        tab_index:index,
        list:[],
        not_more:false
      })
      this.pageList(index)
    }
  }
  onRefresh= ()=>{
    this.setState({ refreshing: true });
    this.setState({
      refreshing: false,
      page : this.state.page+1
    });
    this.pageList();
  }
  componentWillUnmount(){

  }
  render() {
    let ListItem = this.state.list.length>0?this.state.list.map((item,index)=>(
      <div className="list_cont flex-row-between" key={index}>
        <div className="list_cont1 flex-column">
          <div className="list_cont1_top1">{item.reason_name}</div>
          <div className="list_cont1_top2">流水编号: {item.transfer_id}</div>
        </div>
        {/*<div className="list_cont1" style={{color:item.amount>0?'':'#f01010'}}>{item.amount>0?'+'+item.amount:item.amount}</div>*/}
        <div className="list_cont2 flex-column">
          <div className="list_cont1_top3" style={{color:item.amount>0?'':'#f01010'}}>{item.amount>0?'+'+item.amount:item.amount}</div>
          <div className="list_cont1_top2">{item.time}</div>
        </div>
        {/*#f01010*/}
      </div>
    // )) :  ''
    )) :  this.state.no_data_show?<div className="no_data">暂无更多</div>:''
    return (
      <div className="common_list">
        <div className="tab">
          {this.state.tab.length > 0 ? this.state.tab.map((item, index) => (
              <div key={index} className={this.state.tab_index==index?"tab_item tab_item_act":"tab_item"} onClick={this.tab_item.bind(this,index)}>{item}</div>
          )) : ''
          }
        </div>
        <div className="list">
          <PullToRefresh
            ref={el => this.ptr = el}
            style={{
              height: this.state.height,
              overflow: 'auto',
            }}
            indicator={{ deactivate: '上拉可以刷新' }}
            direction={this.state.list.length>0?'up':'down'}
            refreshing={this.state.refreshing}
            onRefresh={this.onRefresh}
          >
            {ListItem}
            {this.state.not_more?<div className="not_more">已经到底部了哦~</div>:this.state.list.length>=10?<div className="not_more">上拉加载更多</div>:''}
          </PullToRefresh>
        </div>
      </div>
    )
  }
}

export default List


