import React from 'react'
import styles from './css.css'
import img from './img/icon01@2x.png'
import oecImg from './img/icon02@2x.png'
import huiImg from './img/icon03@2x.png'
import lineImg from './img/line@2x.png'
import image from './img/images.png'
import {withRouter} from 'react-router-dom'
import {AjaxSend,to_fixed4} from '../../../../../util/util'

class Download extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      openPrice: '',
      quotPrice: '',
      rose:'',
      hui_balance:'',
      OEC_balance:'',
      timer:''
    };
  }

  componentDidMount() {
    this.get_user_data()
    this.time()
  }

  componentWillReceiveProps(nextProps) {

  }
  toReocrd1(){
    var data = {type:10};
    var path = {
      pathname:'/score_detail',
      state:data,
    }
    this.props.history.push(path);
    // window.location.href = '/score_detail'
  }

  toReocrd2(){
    var data = {type:11};
    var path = {
      pathname:'/score_detail',
      state:data,
    }
    this.props.history.push(path);
  }
  time(){
    let self_=this;
    this.state.timer=setInterval(()=>{
      self_.get_user_data();
    },6000)
  }
  componentWillUnmount(){
    clearInterval(this.state.timer)
  }
  get_user_data(){
    var _this = this
    const data = {
      _cmd: 'oec_quot_getQutot'
    };
    AjaxSend(data, (da) => {
      // var changePrice = 0;
      // if(parseFloat(da.data.quotPrice)>parseFloat(da.data.openPrice)){
      //   changePrice = to_fixed4((parseFloat(da.data.quotPrice)-parseFloat(da.data.openPrice)))
      // }else{
      //   changePrice = 0
      // }

      _this.setState({
        openPrice:to_fixed4(da.data.openPrice),
        quotPrice:to_fixed4(da.data.quotPrice),
        rose:to_fixed4(da.data.rose),
      });
    }, (errors) => {
      alert(errors);
    })
    //获取汇积分余额
    AjaxSend({_cmd: 'oec_user_account_getbalance',
      currency:2}, (da) => {
      _this.setState({
        hui_balance:to_fixed4(da.data[1].balance)
      });
    }, (errors) => {
      alert(errors);
    })
    //获取OEC余额
    AjaxSend({_cmd: 'oec_user_account_getbalance',
      currency:102}, (da) => {
      _this.setState({
        OEC_balance:to_fixed4(da.data[102].balance)
      });
    }, (errors) => {
      alert(errors);
    })
  }

  render() {
    return (
      <div className="zz_header_cont1">
        <div className="zz_header_cont flex-column">
          <img src={img} alt=""/>
          <div className="oec_num">{this.state.quotPrice}</div>
          <div className="oec_change">+ {this.state.rose}</div>
        </div>
        <ul className="mine_score flex-row-around">
          <li className="mine_score_li flex-row-around" onClick={this.toReocrd1.bind(this)}>
            <div className="mine_score_li_l flex-column">
              <img src={oecImg} alt=""/>
              <span>我的OEC积分</span>
            </div>
            <div className="mine_score_li_r flex-column">
              <span><span className="mine_score1">{this.state.OEC_balance}</span></span>
            </div>
            <img src={lineImg} alt="" className="line_img"/>
          </li>
          <li className="mine_score_li flex-row-around" onClick={this.toReocrd2.bind(this)}>
            <div className="mine_score_li_l flex-column">
              <img src={huiImg} alt=""/>
              <span>我的汇积分</span>
            </div>
            <div className="mine_score_li_r flex-column">
              <span><span className="mine_score2">{this.state.hui_balance}</span></span>
            </div>
          </li>
        </ul>
        <img src={image} alt="" className="ba_img"/>
      </div>
    )
  }
}


export default withRouter(Download)


