import React from 'react';
import DocumentTitle from "react-document-title";
import './css.css'
import { Toast } from 'antd-mobile';
import treeImg from './img/tree@2x.png'
import {AjaxSend} from "../../util/util"


class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      arr:[1,2]
    }
  }
  componentDidMount() {
    let data = {
      _cmd:'app_messages'
    }
    AjaxSend(data,(da)=>{
        console.log(da)
    },(err)=>{

    })
  }
  render() {
    return (
      <div className="credit_record_cont">
        <DocumentTitle title="信用记录"/>
        <div className="list">
          {this.state.arr.map((i,index)=>(
            <div className="list_item flex-row-between">
              <img src={treeImg} alt=""/>
              <div className="right_cont flex-column">
                <div className="item_time">2018-01-20 19:20:07</div>
                <div className="item_detail flex-column">
                  <div className="item_title_cont flex-row-between">
                    <div className="item_title">都爱泡泡烤鱼现金交易</div>
                    <div className="item_num1">+20</div>
                  </div>
                  <div className="item_text">如果你无法简洁的表达你的想法，那只说明你还不够了解它。</div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default HomeIndex




