import { observable,computed,autorun,action } from 'mobx';
import {Toast} from 'antd-mobile';
class routername {

  @observable RouterName = 0;

  constructor(props) {
    // 一秒递增
    /*setInterval(() => {
      this.timer += 1;
    }, 1000);*/
  }
  // 传入时间戳，开始倒计时
  // 重置计数器
  @action changeRouterName(name) {
    this.RouterName = name;
  }


}
const RouterName = new routername()
export default RouterName



