import React from 'react';
import DocumentTitle from "react-document-title";
import {AjaxSend} from "../../util/util"
import {Toast} from 'antd-mobile'
import Img from './img/img.png'
import './css.css'



class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      img:''
    }
  }
  componentDidMount() {
    let _this = this;
    const data = {
      _cmd: 'app_index',
    };
    AjaxSend(data, (da) => {
      _this.setState({
        img: da.data.info.licence_logo,
      });
    }, (e) => {
      Toast.fail(e);
    })
  }
  render() {
    return (
      <div className="business_license">
        <DocumentTitle title="营业执照"/>
        <div className="list flex-column">
          <img src={this.state.img} alt="" className="user_img"/>
        </div>

      </div>
    );
  }
}

export default HomeIndex




