import React from 'react'
import ReactDom from 'react-dom'
import { ListView,PullToRefresh ,Modal,Toast} from 'antd-mobile'
import DocumentTitle from 'react-document-title'
import { selectModal } from './subpage/selectModal'
import './record.css';
import {AjaxSend,setCookie,formats,get7days,get3MonthBefor,getPreMonth,getToday,to_fixed4,to_fixed2} from '../../../util/util';
function MyBody(props) {
    return (
        <div className="am-list-body my-body">
            <span style={{ display: 'none' }}>you can custom body wrap element</span>
            {props.children}
        </div>
    );
}

const alerts = Modal.alert;
const tap = { undefined : "",0 : "", 1 : 1, 2 : 3 };
let timeData = {start:""};


class Record extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            arr: ['全部', '待兑换', '已兑换'],
            curr : 0,
            isLoading: true,
            isShow: false,
            orderList: [],
        	page : 1,
        	timeIndex : "",
            height: document.documentElement.clientHeight,
            modal1: false,
        };
    }
    componentDidMount() {
        const hei = this.state.height - ReactDom.findDOMNode(this.ptr).offsetTop;
	    setTimeout(() => this.setState({
	      height: hei,
	    }), 0);
	    this.pageList();
    }
    pageList=(params)=>{
    	Toast.loading('Loading...', 30, () => {});
        const data = {
      		_cmd: 'oec_user_order_listPage',
      		bsFlag : 1,
      		status : tap[params]
	    };
	    let orderList = [];
	    if (params == undefined) {
  			orderList = this.state.orderList;
  			data.curPage = this.state.page;
  		}else{
  			data.curPage = 1;
  			this.setState({ page: 1})
  		}
  		if(timeData.start != ''){
  			data.start = timeData.start;
  			data.end = timeData.end;
  		}
	    AjaxSend(data, (da) => {
	      let data = da.data.infos;
	      
	      for (let k in data) {
	      	let list = [];
	      	list = data[k];
	      	list.time = formats(data[k].orderTime);
      		orderList.push(list);
	      }
	      this.setState({
			orderList : orderList,
			active : false
	      });
	      Toast.hide();
	    }, (e) => {
	      alert(e);
	    })
    }
    onRefresh= ()=>{
      	this.setState({ refreshing: true });
      	setTimeout(() => {
        	this.setState({ 
        		refreshing: false, 
        		page : this.state.page+1
        	});
        	this.pageList();
      	}, 1000);
	}
    handleTabClick=(i)=> {
        this.setState({ curr: i})
        this.pageList(i);
    }
    handleSelect=(val=false)=>{
        this.setState({ isShow: val, });
    }
    handleClickDrop=()=>{
        let val=false
        if(!this.state.isShow){
            val=true
        }
        this.handleSelect(val)
    }
    timeParams =(index)=>{
    	var time = {
    		0 : Date.parse(getToday()),
    		1 : Date.parse(new Date(get7days())),
    		2 : Date.parse(new Date(getPreMonth())),
    		3 : Date.parse(new Date(get3MonthBefor())),
    	}
    	if (this.state.timeIndex === index) {
    		this.setState({ timeIndex: ""});
    		timeData.start = "";
    	} else{
    		timeData.start = time[index];
    		timeData.end = Date.parse(new Date());
    		this.setState({ 
    			timeIndex: index,
    		});
    	}
    	console.log(timeData);
    	this.pageList(this.state.curr);
    	this.handleSelect(false);
    }
    cancelOrder = (datas)=>{
    	let data = {
      		_cmd: 'oec_user_order_cancel',
      		contractType : 1,
      		orderId : datas.orderNo
	    };
    	alerts('取消兑换？', '', [
		    { text: '取消', onPress: () => console.log("取消"), style: 'default' },
		    { text: '确认', onPress: () => 
		    	AjaxSend(data, (da) => {
		    		Toast.success('取消兑换成功！', 1);
		    		this.pageList(this.state.curr);
			    }, (e) => {
			      alert(e);
			    })
		    },
		  ]);
    }
    ToDetail=(datas)=>{
    	let data={
    		orderId : datas.orderNo
    	};
    	let path = {
    		pathname : "/dealExchangeDetail",
    		state : data,
    	}
    	this.props.history.push(path);
    }
    render() {
    	let status = {
    		1 : "待兑换",
    		2 : "待兑换",
    		3 : "已兑换",
    		4 : "已取消",
    	}
    	let that = this;
        let consumptionList = this.state.orderList.length>0?this.state.orderList.map(function (item,index) {
            return (
                <div key={item.orderNo} style={{ padding: '0 15px' }} className="record_list_cont">
                    <div className="record_list_item1 flex-row-between">
                        <div className="record_time">{item.time}</div>
                        <div className={item.orderStatus == 1 || item.orderStatus == 2 ?"record_status statusColor":"record_status"}>{status[item.orderStatus]}</div>
                    </div>
                    <div className="record_list_item2 flex-row-between">
                        <div className="record_num flex-column">
                            <div className="record_num_title">预约价格</div>
                            <div className="record_num_value">{to_fixed4(item.orderPrice/10000)}</div>
                        </div>
                        <div className="record_num flex-column">
                            <div className="record_num_title">预约数量</div>
                            <div className="record_num_value">{to_fixed4(item.orderQty/10000)}</div>
                        </div>
                        <div className="record_num flex-column">
                            <div className="record_num_title">成交数量</div>
                            <div className="record_num_value">{to_fixed4(item.matchQty/10000)}</div>
                        </div>
                        <div className="record_num flex-column">
                            <div className="record_num_title">待兑换数量</div>
                            <div className="record_num_value">{to_fixed4(item.orderQty/10000 - item.matchQty/10000)}</div>
                        </div>
                    </div>
                    <div className="record_list_item3 flex-row-between">
                        {/*<div className={item.orderType != 3 && item.orderStatus != 3 && item.orderType != 5 && item.orderStatus != 4 ?"record_num_btn1":"none"}*/}
                        {/*onClick={()=>that.cancelOrder(item)}>取消兑换</div>*/}
                        <div className={item.matchQty/10000 > 0?"record_num_btn2":"record_num_btn2 none"}  onClick={()=>that.ToDetail(item)}>兑换详情</div>
                    </div>
                </div>
            );
        }): <div className="no_data">暂无更多</div>;

		const timeText = [
			{key: 0, value: "今日"},
            {key: 1, value: "最近一周"},
            {key: 2, value: "最近一个月"},
            {key: 3, value: "最近三个月"}
        ]
		
		let timeList = timeText.map(function (item,index) {
			return (
				<div key={item.key} className={that.state.timeIndex === item.key?"sel_item active":"sel_item"}
				onClick={()=>that.timeParams(item.key)}>{item.value}</div>
			)
		})
        return (
            <DocumentTitle title="记录">
                <div >
                        <div className={this.state.isShow ?"deal_select_modal":"none"}>
                          <div className="up">
                             {timeList}
                          </div>
                          <div className={this.state.isShow ?"down":"none"}
                           onClick={()=>this.handleSelect(false)}>
                          </div>
                        </div>
                    <div className="deal_record">
                        <div className="record_header">
                            <div className="tab_title">
                                <ul>
                                    {this.state.arr.map((x, i) =>
                                        <li key={i}
                                            className={i === this.state.curr ? 'select' : ''}
                                            onClick={() => this.handleTabClick(i)}
                                        >
                                            <span>{x}</span>
                                            <span className="deal_record_border"></span>
                                        </li>
                                    )}
                                </ul>
                            </div>
                            <div className="tab_drop">
                                <img src={require('./img/drop_down.png')} 
                                  onClick={()=>this.handleClickDrop()}
                                />
                            </div>
                        </div>
                        <PullToRefresh
					        ref={el => this.ptr = el}
					        style={{
					          height: this.state.height,
					          overflow: 'auto',
					        }}
					        indicator={{ deactivate: '上拉可以刷新' }}
					        direction='up'
					        refreshing={this.state.refreshing}
					        onRefresh={this.onRefresh}
					      >
					        {consumptionList}
					      </PullToRefresh>

                    </div>
                </div>
            </DocumentTitle>
        )
    }
}


export default Record


