import React from 'react';
import ReactDom from 'react-dom'
import DocumentTitle from "react-document-title";
import { PullToRefresh ,Toast} from 'antd-mobile'
import {AjaxSend} from "../../util/util"
import './css.css'



class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={
      list:[],
      refreshing: false,
      page : 1,
      no_data_show:false,
      not_more:false,
      height: document.documentElement.clientHeight,
    }
  }
  componentDidMount() {
    const height = this.state.height - ReactDom.findDOMNode(this.ptr).offsetTop;
    setTimeout(() => this.setState({
      height: height,
    }), 0);
    setTimeout(() => this.setState({
      no_data_show: true,
    }), 200);
    this.pageList()
  }
  pageList=(params)=>{
    let _this = this
    Toast.loading('Loading...', 30, () => {});
    const data = {
      _cmd:'app_order_history',
    }
    let total_list = [];
    if (params == undefined) {
      total_list = this.state.list;
      data.page = this.state.page;
    }else{
      data.page = 1;
      this.setState({ page: 1})
    }
    AjaxSend(data, (da) => {
      let data = da.data.list.data;
      for (let i in data) {
        let list = [];
        list = data[i];
        total_list.push(list);
      }
      Toast.hide();
      if(this.state.list.length>0 && da.data.list.data.length==0){
        this.setState({
          not_more : true,
        });
        return
      }
      _this.setState({
        list : total_list,
      });
      console.log(this.state.list)
    }, (e) => {
      alert(e);
    })
  }
  onRefresh= ()=>{
    this.setState({ refreshing: true });
    this.setState({
      refreshing: false,
      page : this.state.page+1
    });
    this.pageList();
  }
  render() {
    let ListItem = this.state.list.length>0?this.state.list.map((item,index)=>(
      <div className="list_item flex-column" key={index}>
        <div className="item flex-row-between">
          <div className="item_text1">订单总价</div>
          <div className="item_text2">￥{item.amount}</div>
        </div>
        <div className="item1 flex-row-between">
          <div className="item_text3">让利金额</div>
          <div className="item_text5">￥{item.damage_money}</div>
        </div>
        <div className="item1 flex-row-between">
          <div className="item_text3">商家订单收入</div>
          <div className="item_text5">￥{item.merchant_money}</div>
        </div>
        <div className="item2 flex-column">
          <div className="item_text4">订单编号：{item.amount}</div>
          <div className="item_text4">付款时间： {item.pay_time}</div>
        </div>
      </div>
    )): this.state.no_data_show?<div className="no_data">暂无更多</div>:''
    return (
      <div className="business_order">
        <DocumentTitle title="我的商家订单"/>
        <div className="list">
          <PullToRefresh
            ref={el => this.ptr = el}
            style={{
              height: this.state.height,
              overflow: 'auto',
            }}
            indicator={{ deactivate: '上拉可以刷新' }}
            direction={this.state.list.length>0?'up':'down'}
            refreshing={this.state.refreshing}
            onRefresh={this.onRefresh}
          >
            {ListItem}
            {this.state.not_more?<div className="not_more">已经到底部了哦~</div>:this.state.list.length>10?<div className="not_more">上拉加载更多</div>:''}
          </PullToRefresh>
        </div>

      </div>
    );
  }
}

export default HomeIndex




